/* Estilos principales de la aplicación */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #3e5338; /* Fondo verde oscuro */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Contenedor principal */
.App {
  text-align: center;
  flex: 1; /* Para asegurar que el contenido crezca con la ventana */
}

/* Logo de la aplicación */
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

/* Cabecera */
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* Links */
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Contenedor del formulario */
.form-container {
  max-width: 400px;
  margin: 50px auto;
  padding: 30px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* Estilo de los títulos */
h1 {
  margin-bottom: 20px;
  font-size: 1.8rem;
  color: #333;
}

h2 {
  margin-top: 20px;
  color: #4CAF50;
}

/* Labels del formulario */
form label {
  display: block;
  margin: 10px 0 5px;
  text-align: left;
  font-weight: bold;
}

/* Campos de entrada */
form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}

/* Botón */
button {
  width: 100%;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

/* Estilo para el resultado */
.result {
  margin-top: 15px;
  font-size: 1rem;
  font-weight: bold;
  color: #333;
}
